import React from "react";
import { Text, Image, StyleSheet, View, Button, TouchableOpacity, ImagePropTypes, Touchable, ImageBackground } from "react-native";
import { Dimensions } from 'react-native';
import AppLoading from 'expo-app-loading';
import {useFonts, PressStart2P_400Regular} from '@expo-google-fonts/press-start-2p'



const HomeScreen = (props:any) => {

    let [fontsLoaded] = useFonts({
        PressStart2P_400Regular,
      });
    
      if (!fontsLoaded) {
        return <AppLoading />;
      }

    return <View style={styles.viewStyle}>

        <ImageBackground style={styles.imageStyle} source={require('../../assets/examplebanner.gif')}>

            <TouchableOpacity style={styles.buttonStyle}>
                <Text style={styles.textStyle} >Choose Your Own Audioventure!</Text>                
            </TouchableOpacity>

        </ImageBackground>


    </View>
};

const win = Dimensions.get('window');
const BACKGROUND_HEIGHT = 663;
const BACKGROUND_WIDTH = 1179;
const ratio = win.height/BACKGROUND_HEIGHT;
const styles = StyleSheet.create({
    textStyle: {
        fontFamily: 'PressStart2P_400Regular',
        fontSize: 34,
        textAlign: 'center',
        color: 'white',
        opacity: 1,
        lineHeight: 34 + 34*(.15)
    },
    viewStyle: {
        // flexDirection: "column",
        justifyContent: 'center',
        backgroundColor: 'black',
        alignItems: 'center',
    },
    imageStyle: {
        justifyContent: 'center',
        alignItems: 'center',
        height: win.height,
        width: ratio * BACKGROUND_WIDTH,
        resizeMode: "center",
        // ...StyleSheet.absoluteFillObject,
        overflow: 'hidden'
    },
    buttonStyle: {
        borderWidth: 5,
        borderColor: '#b4a7d6',
        backgroundColor: '#8e7cc3c6',
        width: win.width/2.5,
        height: win.height/4,
        borderRadius: 5,
        justifyContent: 'center',
    }
})
export default HomeScreen;

